<template>
    <v-select :on-change="setSelect" label="brandCname" :debounce="500" :on-search="setOptions" placeholder="搜索品牌" :options="brandList"></v-select>
</template>
<script type="text/javascript">
    import vSelect from '../../../../components/common/vue-select/src/index.js'
    export default{
        components:{
            vSelect
        },
        data(){
            return{
                brandList:[],
                setBrand:[
                    {brandCname:'品牌1',id:'1'},
                    {brandCname:'品牌2',id:'2'},
                    {brandCname:'品牌3',id:'3'},
                    {brandCname:'品牌4',id:'4'}
                ]
            }
        },
        methods:{
            setSelect:function(val){
                console.log(val);
            },
            setOptions:function(val){
                console.log(val);
                this.$set('brandList',this.setBrand);
            }
        }
    }
</script>



